import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/index'
import Topic from '../components/tipic'
import User from '../components/user'
import Login from '../components/login'
import About from '../components/about'
import Fabu from '../components/fabu'
Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
    },
    {
      path:'/topic/:id',
      name:'topic',
      component:Topic,
    },
    {
      path:'/user/:loginname',
      name:'user',
      component:User,
    },
    {
      path:'/login',
      name:'login',
      component:Login,
    },
    {
      path:'/about',
      name:'about',
      component:About,
    },
    {
      path:'/fabu',
      name:'fabu',
      component:Fabu,
      meta:{
        requireAuth:true
      }
    }

  ]
})




