<template>
<div>
     <v-header @showclick="showclick" :title="title"  :class="[{'headx-right':popupVisible!=false},{'headx-left':popupVisible!=true}]"></v-header>
    <mt-popup
     v-model="popupVisible"
     position="left"
     class="mtnav">
     <slot>
       <v-nav></v-nav>
    </slot>
   </mt-popup>
   <div class="fabu">
       <div class="fabu-inner">
           <span>选择板块:</span>
           <select class="selection" v-model="tab">
               <option v-for="list in selection" :value="list.tab">{{list.label}}</option>      
           </select>
       </div>
       <div class="fabu-inner">
           <input class="title" v-model="fabutitle" type="texy"  placeholder="标题10个字以上">
       </div>
       <textarea v-model="txt" class="textbody" rows="34">ddddd</textarea>
       <mt-button class="butfabu" @click="onfabu" size="small">发布</mt-button>
   </div>
</div>
</template>
<script>
import VHeader from '../components/header'
import VNav from '../components/nav'
export default {
      components:{
    VHeader,
    VNav,
  },
  name: "component_name",
  data () {
    return {
        popupVisible:false,
        title:'发布',
        tab:'share',
        fabutitle:'',
        txt:'',
        accesstoken:this.$cookiestore.getCookie('token'),
        selection:[
            {
                tab:'share',
                label:'分享'
            },
            {
                tab:'ask',
                label:'问答'
            },
              {
                tab:'job',
                label:'招聘'
            }
        ],
    };
  },
  methods:{
      showclick(){
      this.popupVisible=true
    },
    onfabu(){
        if(this.txt==''||this.fabutitle.length<10){
            this.$toast("输入不规范")
        }else{
            this.$http({
                url:'https://cnodejs.org/api/v1/topics',
                method:'post',
                data:this.qs.stringify({tab:this.tab,title:this.fabutitle,content:this.txt,accesstoken:this.accesstoken})
            })
            .then((res)=>{
                console.log("ok")

            })
            .catch((error)=>{
                console.log(error)
            })
        }
    }
  },
  mounted(){
// console.log(this.tab,this.fabutitle,this.txt)
  }
}
</script>
<style scoped>
.fabu{
    margin-top: 40px;
}
.fabu-inner{
    padding: 10px 15px;
    border-bottom: 1px solid #e1e1e1;
}
.selection{
    width: 80%;
    height: 25px;
    font-size: 16px;
}
.title{
    font-size: 16px;
    width: 100%;
    border: none;
    outline: medium;
}
.textbody{
    width: 98%;
}
.butfabu{
  display: block;
  width: 100%;
  position: fixed;
  bottom: 0;
  background: #9ACD32
}
</style>


