<template>
<div>
    <v-header @showclick="showclick" :title="title" :class="[{'headx-right':popupVisible!=false},{'headx-left':popupVisible!=true}]"></v-header>
    <mt-popup
     v-model="popupVisible"
     position="left"
     class="mtnav">
     <slot>
       <v-nav></v-nav>
    </slot>
   </mt-popup>
   <div class="userhead">
     <img :src="user.avatar_url"><br>
     <span>{{user.loginname}}</span>
     <div class="userxinxi">
       <span class="shijian">创建时间:{{dealCommentTime(user.create_at)}}</span>
       <span class="jifen">积分:{{user.score}}$</span>
     </div>
   </div>
   <div class="content">
     <ul class="tab-head">
       <li class="selected" @click="changeTab(0)">最近回复</li>
       <li @click="changeTab(1)">最近创建</li>
     </ul>
     <div class="tab-content">
       <div class="show">
         <span class="inner" v-for="replies in user.recent_replies">
           <router-link :to="{name:'user',params:{loginname:replies.author.loginname}}">
           <img :src="replies.author.avatar_url">
           </router-link>
           <router-link :to="{name:'topic',params:{id:replies.id}}">
           <span class="title">{{replies.title}}</span>
           <span>{{replies.author.loginname}}</span>
           </router-link>
           <span class="last_reply_at">{{dealCommentTime(replies.last_reply_at)}}</span>
         </span>
       </div>
         <div>
         <span class="inner" v-for="topics in user.recent_topics">
           <router-link :to="{name:'user',params:{loginname:topics.author.loginname}}">
           <img :src="topics.author.avatar_url">
           </router-link>
           <router-link :to="{name:'topic',params:{id:topics.id}}">
           <span class="title">{{topics.title}}</span>
           <span>{{topics.author.loginname}}</span>
           </router-link>
           <span class="last_reply_at">{{dealCommentTime(topics.last_reply_at)}}</span>
         </span>
       </div>
       <div>
       </div>
     </div>
   </div>
    <mt-button class="del" @click="del" v-if="this.$cookiestore.getCookie('loginname')!=null&&this.$cookiestore.getCookie('loginname')==this.$route.params.loginname" size="small">退出</mt-button>
</div>
</template>
<script>
import VHeader from '../components/header'
import VNav from '../components/nav'
export default {
  components:{
    VHeader,
    VNav,
  },
  name: "component_name",
  data () {
    return {
        popupVisible:false,
        title:'用户信息',
        user:{
          author:{
            loginname:''
          },
          create_at:'2018-10-27T14:33:14.694Z',
        },
    };
  },
  methods:{
       del(){
          this.$cookiestore.delCookie('loginname')
          this.$cookiestore.delCookie('avatar_url')
          this.$cookiestore.delCookie('token')
          this.$router.push({path:'/'})
      },
        //转化时间
     dealCommentTime(time) {
             return String(time).match(/.{16}/)[0].replace(/.{2}/, '').replace(/[T]/, ' ');
        },
    showclick(){
      this.popupVisible=true
    },
    changeTab(index){
      var tabs = document.getElementsByClassName('tab-head')[0].getElementsByTagName('li');
      var  contents = document.getElementsByClassName('tab-content')[0].getElementsByTagName('div');
      for(var i=0;i<tabs.length;i++){
        if(i===index){
          tabs[i].className='selected';
          contents[i].className='show'
        }else{
          tabs[i].className='';
          contents[i].classList='';
        }
      }
    },
    getdatauser(){
      // let loginname=this.$route.params.loginname;
      if(!this.$route.params.loginname){
        this.$toast("缺少用户参数");
        this.$router.push({path:'/'})
      }else{
        this.$http({
        url:'https://cnodejs.org/api/v1/user/'+this.$route.params.loginname,
        method:'get',
      })
      .then((res)=>{
        this.user=res.data.data
        console.log(this.user);
        
        // this.replies=res.data.data.recent_replies;
        // this.topics=res.data.data.recent_topics;
      })
      .catch((error)=>{
        console.log(error)
      })
      }
    }
  },
  watch:{
    '$route'(to,from){
      this.getdatauser()
    }
  },
  mounted(){
    this.getdatauser()
  }
}
</script>
<style scoped>
.userhead{
  margin-top: 40px;
  background: #e7e7e7;
  text-align: center;
}
.userhead img{
  width: 100px;
  height: 100px;
  margin-top: 15px;
  border-radius:50%;
  display: inline-block; 
}
.userxinxi{
  margin-top: 15px 0;
  padding: 15px 0;
}
.userxinxi::after{
    content:"";/*设置内容为空*/
    height:0;/*高度为0*/
    line-height:0;/*行高为0*/
    display:block;/*将文本转为块级元素*/
    visibility:hidden;/*将元素隐藏*/
    clear:both;/*清除浮动*/
}
.userxinxi>span{
  display: block;
  width: 50%;
}
.shijian{
  float: left;
}
.jifen{
  float: right;
}
.tab-head{
  border-bottom: 1px solid #e1e1e1;
  height: 46px;;
}
.content li{
  display: block;
  width: 50%;
  float: left;
  text-align: center;
  padding: 13.5px 0;
}
.selected{
  border-bottom:2px solid #ff5a5f;
}
.tab-content div{
  display: none;
  margin-top: 15px;
}
.tab-content .show{
  display: block;
}
.tab-content .inner{
  display: block;
  border-bottom: 1px solid #e1e1e1;
  padding: 15px;
}
.inner a{
  display: inline-block;
  vertical-align: middle;
  color: #333;
}
.inner img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.title{
  display: block;
  width: 230px;
  white-space:nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: black;
  font-size: 18px;
  font-weight: 600;
}
.last_reply_at{
  float: right;
}
.del{
  display: block;
  width: 100%;
  position: fixed;
  bottom: 0;
  background: #9ACD32
}

</style>


