<template>
<mt-header fixed :title="title" class="header">
        <mt-button slot="left" @click="showclick">
          <img src="../assets/images/nav_icon.png" width="20" height="15">
        </mt-button>
     <mt-button slot="right" class="zhuafa" v-if="iconboole" @click="zhuanfa">
       <!-- <img src="../assets/images/logo.png" width="20" height="15"> -->
       <span class="iconfont icon-zhuanfa1"></span>
     </mt-button>
     <mt-button slot="right" class="fabu" @click="fabu">
       <!-- <img src="../assets/images/logo.png" width="20" height="15"> -->
       <i class="iconfont icon-noticeplus"></i>
     </mt-button>
    </mt-header>
</template>
<script>
export default {
  name: "component_name",
  props:{
    title:{},
    iconboole:{
      type:Boolean,
      default:false
    }
  },
  data () {
    return {

    };
  },
  methods:{
      showclick(){
          this.$emit('showclick')
      },
      zhuanfa(){
        this.$emit('onzhuanfa')
      },
      fabu(){
        this.$router.push({path:'/fabu'})
      }
  }
}
</script>
<style scoped>
.header{
  color: #000;
  background: #fff;
  border-bottom: 1px solid #e1e1e1;
}
.zhuafa{
  margin-right: 20px;
}
 /* i{
width: 20px;
height: 41px;
} */
</style>


