<template>
  <div id="app">
   <div class="backtop iconfont icon-fanhuidingbu" @click="topclick" v-if="topshow"></div>
   <router-view></router-view>
  </div>
</template>

<script>
export default {
  data(){
    return{
          topshow:false,   
    }
  },
  components:{
   
  },


  methods:{
    topclick(){
      window.scroll(0,0)
    },
    topshowclick(){
      if(document.documentElement.scrollTop>0){
        this.topshow=true
      }else{
        this.topshow=false
      }
    }
  },
  
mounted(){
  // this.$toast('DDDD')
  window.addEventListener('scroll',this.topshowclick)
}
}
</script>

<style>

/* 初始化页样式 */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
}
body{
  background: #fff;
  font-family:  "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma, "Hiragino Sans GB", STHeiti, sans-serif;
  font-size: 14px;
}
ul,ol{
  list-style: none;
}
a{
  text-decoration: none;
  cursor: pointer;
}
img{
      vertical-align: middle;
      max-width: 100%;
}


.mtnav{
    width: 200px;
    height: 100%;
   
}
.headx-right{
  transform: translateX(200px);
  transition: all .3s ease;
}
.headx-left{
  transform: translateX(0px);
  transition: all .3s ease;
}
/* 全局加载动画 */
.loading{
  position: absolute;
  top: 50%;
  left: 50%;
}
/* 回到顶部 */
.backtop{
  position: fixed;
  top: 85%;
  right: 0;
  font-size: 40px;
  cursor: pointer;
  color: #80bd01;
  /* word-wrap: break-all; */
}

</style>



