<template>
<div>
    <v-header @showclick="showclick" :title="getTitleStr(title)" :class="[{'headx-right':popupVisible!=false},{'headx-left':popupVisible!=true}]"></v-header>
    <mt-popup
     v-model="popupVisible"
     position="left"
     class="mtnav">
     <slot>
       <v-nav></v-nav>
    </slot>
   </mt-popup>
<div class="index">
    <div class="index-list" v-for="item in topicdata">
        <router-link  class="user_avatar" :to="{name:'user',params:{loginname:item.author.loginname}}"><img :src="item.author.avatar_url"></router-link>
        <span class="reply-count">
            <span class="reply-countleft">{{item.reply_count}}</span>
            <span class="reply-countleft">/</span>
            <span class="reply-countrihgt">{{item.visit_count}}</span>
        </span>
        <span class="topic_title">
            <span :class="[{'titlespan1':item.top!=false},{'titlespan2':item.good!=false},{'titlespan':item.top!=true&&item.good!=true}]">{{func(item)}}</span>
            <router-link class="title" :to="{name:'topic',params:{id:item.id}}">{{item.title}}</router-link>
        </span>
        <span class="last_time" :to="{path:'/'}">{{dealCommentTime(item.last_reply_at)}}</span> 
    </div>
    <mt-spinner type="double-bounce" color="#80bd01" :size="40" v-if="loadingshow" class="loading"></mt-spinner>
</div>
</div>
</template>
<script>
import VHeader from '../components/header'
import VNav from '../components/nav'
export default {
    components:{
    VHeader,
    VNav,
  },
  name: "component_name",
  data () {
    return {
        limit:20,
        topicdata:[],
        loadingshow:true,    
        popupVisible:false,
        title:'',
    };
  },
  // created(){
  //   this.loadingshow=false
  // },
  methods:{
       showclick(){
      this.popupVisible=true
    },
       getTitleStr(tab) {
                let str = '';
                switch (tab) {
                    case 'share':
                        str = '分享';
                        break;
                    case 'ask':
                        str = '问答';
                        break;
                    case 'job':
                        str = '招聘';
                        break;
                    case 'good':
                        str = '精华';
                        break;
                        case 'zhuti':
                        str='主题'
                        break;
                        case 'qqq':
                        str="主题"
                        break;
                    default:
                        str = '全部';
                        break;
                }
                return str;
            },
        //转化时间
     dealCommentTime(time) {
             return String(time).match(/.{16}/)[0].replace(/.{2}/, '').replace(/[T]/, ' ');
        },
       getData(){
        this.$http({
        url:'https://cnodejs.org/api/v1/topics',
        method:'get',
        params:{
          page:1,
          limit:this.limit,
          tab:this.$route.query.tab 
        }
      })
      .then((res)=>{
        this.topicdata=res.data.data
        this.loadingshow=false
      })
      .catch((error)=>{
        console.log(error)
      })
    },
    func(item){
      if(item.top!=false){
        return "置顶"
      }
      if(item.good!=false){
        return "精华"
      }
      if(item.tab=="ask"){
        return "问答"
      }
      if(item.tab=="share"){
        return "分享"
      }
      if(item.tab=="job"){
        return "招聘"
      }
  },
     scrollMethod(){
            var scrollTop=document.documentElement.scrollTop;
            var clientHeight =document.documentElement.clientHeight;
            var scrollHeigh=document.documentElement.scrollHeight;
            if(scrollTop+clientHeight+1>=scrollHeigh){
                  this.limit+=20;
                 this.getData();
                   
    }
     },
  },
  watch: {    
    '$route' (to, from) {   
        //  this.$router.go(0);  
        this.limit=20;
        console.log(this.limit)
        window.scroll(0,0);
        this.getData(); 
        this.title=this.$route.query.tab;
        this.popupVisible=false;
 }},
     mounted(){
        this.getData();
        window.addEventListener('scroll',this.scrollMethod)
     }
}
</script>
<style scoped>

.index{
    padding-top: 40px;
}
.index-list{
    border-bottom: 1px solid #e1e1e1;
    padding: 10px 15px;

}
.index-list::after{
    content:"";/*设置内容为空*/
    height:0;/*高度为0*/
    line-height:0;/*行高为0*/
    display:block;/*将文本转为块级元素*/
    visibility:hidden;/*将元素隐藏*/
    clear:both;/*清除浮动*/
}

.user_avatar{
    display: inline-block;
}
.user_avatar>img{
    width: 30px;
    height: 30px;
    border-radius: 50%
}
.reply-count{
    display: inline-block;
    width: 70px;
    line-height: 30px;
    text-align: center;
}
.reply-countleft{
    color: #9e78c0;
}
.reply-countrihgt{
    font-size: 10px;
    color: #b4b4b4;
}
.topic_title{
    display: inline-block;
    line-height: 30px;

}
.topic_title span{
     padding: 2px 4px;
     font-size: 12px;
     color: #fff;
     border-radius:20%
}
.titlespan1{
    background: #e74c3c;   
}
.titlespan2{
    background: #80bd01;
}
.titlespan{
    background: #999;  
}
.title{
    font-size: 16px;
    color: #333;
    display: inline-block;
    width: 160px;
    white-space:nowrap;
    line-height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align:top;/*定义行内元素的基线相对于该元素所在行的基线的垂直对齐。*/
   
}
.title:hover{
    text-decoration:underline;
    text-decoration-color: #888;
    color: #000;
}
.last_time{
    display: inline-block;
    line-height: 30px;
    float: right;
    color: #778087;
    font-size: 11px;
}

</style>


