<template>
<div class="nav">
  <div class="login">
    <router-link class="dl iconfont icon-denglu1" :to="{path:'/login'}" v-if="loginname==null">登陆</router-link>
    <router-link class="user" :to="{name:'user',params:{loginname:loginname}}" v-if="loginname!=null">
          <img :src="avatar_url">
          <span>{{loginname}}</span>
    </router-link>
    <!-- <mt-button @click="del" v-if="loginname!=null" size="small">退出</mt-button> -->
  </div>
  <div class="navlist">
    <router-link class="iconfont icon-quanbu" :to="{path:'/',query:{tab:'all'}}">全部</router-link>
    <router-link class="iconfont icon-jinghua" :to="{path:'/',query:{tab:'good'}}">精华</router-link>
    <router-link class="iconfont icon-fenxiang" :to="{path:'/',query:{tab:'share'}}">分享</router-link>
    <router-link class="iconfont icon-57" :to="{path:'/',query:{tab:'ask'}}">问答</router-link>
    <router-link class="iconfont icon-zhaopin" :to="{path:'/',query:{tab:'job'}}">招聘</router-link>
    <router-link class="iconfont icon-guanyu" :to="{path:'/about'}">关于</router-link>
  </div>
</div>
       
</template>
<script>
export default {
  name: "component_name",
  data () {
    return {
       loginname:this.$cookiestore.getCookie('loginname'),
       avatar_url:this.$cookiestore.getCookie('avatar_url')
    };
  },
  methods:{

  },
  mounted(){
    // this.del();

  }
  
 
}
</script>
<style scoped>
.login{
  padding:3% 10%;
  font-size: 15px;
  border-bottom: 1px solid #e1e1e1;
}
 .login .dl{
   display:block;
   height: 44px;
   line-height: 44px;
   color: #000;
 } 
 .login .user{
    display:block;
   height: 44px;
   line-height: 44px;
   color: #000;
 }
  .login img{
    width: 44px;
    height: 44px;
    margin-right:8px; 
    border-radius: 50%;
  }

  /* .login span{
    display: inline-block;
    padding-left: 48px;
    line-height: 24px;
  } */
.navlist{
  padding: 10%;
}
.navlist a{
  display: block;
  padding: 10% 0;
  font-weight: bold;
  color: #333;
  text-align: left;
}
.boomtbor{
  border-top: 1px solid #e1e1e1;
}
</style>


