<template>
 <div>
    <v-header @onzhuanfa="zhuanfaclick" @showclick="showclick" :title="title" :iconboole="iconboole" :class="[{'headx-right':popupVisible!=false},{'headx-left':popupVisible!=true}]"></v-header>
    <mt-popup
     v-model="popupVisible"
     position="left"
     class="mtnav">
     <slot>
       <v-nav></v-nav>
    </slot>
   </mt-popup>
<div class="topic">
  <div class="topic-panel">
    <div class="heade">
      <h2>{{article.title}}</h2>
      <img class="author-info" :src="article.author.avatar_url">
      <div class="col">
        <span>{{article.author.loginname}}</span>
        <span>发布于:{{dealCommentTime(article.create_at)}}</span>
      </div>
      <div class="right">
        <span class="tab">{{func(article)}}</span>
        <span>{{article.visit_count}}次浏览</span>
      </div>
    </div>
    <div class="content" v-html="article.content">
    </div>
  </div>
  <div class="topic-panel">
    <h3>{{article.reply_count}}&nbsp;&nbsp;条回复</h3>
    <div class="cell" v-for="item in article.replies">
      <div class="replies">
        <router-link :to="{name:'user',params:{loginname:item.author.loginname}}"><img class="userimg" :src="item.author.avatar_url"></router-link>
        <span>{{item.author.loginname}}</span>
        <span class="dianzan iconfont icon-dianzan" v-if="item.ups.length>0">{{item.ups.length}}</span>
      </div>
      <div class="replies-content" v-html="item.content"></div>
    </div>
  </div>
  <mt-spinner type="double-bounce" color="#80bd01" :size="40" v-if="loadingshow" class="loading"></mt-spinner>
</div>
<mt-popup
     v-model="popupVisible2"
     position="bottom" class="zhuanfa">
     <div class="zhuanfa-content" v-if="ishowfenxiang">
       <img src="../assets//images/qq.jpg" @click="ishow">
       <img src="../assets//images/weixin.jpg" @click="ishow">
       <img src="../assets//images/weibo.jpg" @click="ishow">
     </div>
     <div class="lianjie" v-if="ishowlianjie">
       <span>{{lianjie}}</span>
     </div>
   </mt-popup>
</div>
</template>
<script>
import VHeader from '../components/header'
import VNav from '../components/nav'
export default {
  components:{
    VHeader,
    VNav,
  },
  name: "component_name",
  data () {
    return {
     article: {
       author:{

       },
       create_at:'2018-10-27T14:33:14.694Z',
     },
     loadingshow:true,
     popupVisible:false,
     popupVisible2:false,
     title:'主题',
     iconboole:true,
     ishowfenxiang:true,
     ishowlianjie:false,
     lianjie:window.location.href  
    };
  },

  created(){
    this.getdata()
  },
  methods:{

    ishow(){
      this.ishowfenxiang=false
      this.ishowlianjie=true
    },
    zhuanfaclick(){
      this.popupVisible2=true
    },
       showclick(){
      this.popupVisible=true
    },
    //转化时间
     dealCommentTime(time) {
             return String(time).match(/.{16}/)[0].replace(/.{2}/, '').replace(/[T]/, ' ');
        },
    func(item){
      if(item.top!=false){
        return "置顶"
      }
      if(item.good!=false){
        return "精华"
      }
      if(item.tab=="ask"){
        return "问答"
      }
      if(item.tab=="share"){
        return "分享"
      }
      if(item.tab=="job"){
        return "招聘"
      }
  },
    getdata(){
      this.$http({
        url:'https://cnodejs.org/api/v1/topic/'+this.$route.params.id,
        method:'get'
      })
      .then((res)=>{
        this.article=res.data.data
        this.loadingshow=false
      })
      .catch((error)=>{
        console.log(error)
      })
    }
  }
}
</script>
<style scoped>
.topic{
  margin-top: 44px;
}
.heade{
  padding: 0 10px;
  border-bottom: 1px solid #e1e1e1;
  font-size: 12px;
  color: #2c3e50;
}
h2{
  padding:  5px 0;
  margin-bottom: 15px;
  font-size: 18px;
  color: #2c3e50;
  background: #f0f0f0;
  
}
.author-info {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 15px;
}
.col{
  display: inline-block;
  vertical-align:middle;
}
.col>span{
  display: block;
}
.right{
  display: inline-block;
  vertical-align:middle;
  float: right;
}
.right>span{
   display: block;
}
.tab{
  background: #80bd01;
  text-align: center;
  border-radius: 6px;
  color: #fff;
}
.content{
  padding:15px;
  color: #333;
  margin-top: 15px;
  border-bottom: 1px solid #e1e1e1;
  line-height: 1.6;
  overflow: hidden;
}
h3{
  padding: 10px 15px;
  border-bottom: 1px solid #e1e1e1;
  font-size: 16px;
  color: #2c3e50;
}
.cell{
   border-bottom: 1px solid #e1e1e1;
}
.replies{
  padding: 10px 15px;
  color: #626262;
}
.replies>a{
  display: inline-block;
    margin-right: 10px
}
.userimg{
  width: 40px;
  height: 40px;
  border-radius: 20%;
}
.dianzan{
  display: inline-block;
  float: right;
  color: gray;
}
.replies-content{
  padding: 5px 10px;
}
.zhuanfa{
  width: 100%;
  height: 14%;
  background: #f0f0f0;
}
.lianjie{
   height: 100%;
  text-align: center;
  line-height: 7;
  color: darkslategrey
 
}
.zhuanfa-content {
  height: 100%;
  padding: 5px 15px;
  text-align: center;
}
.zhuanfa-content img{
  display: inline-block;
  width: 20%;
  height: 70%;
  padding: 20px;
}
</style>


