<template>
<div>
    <v-header @showclick="showclick" :title="title" :class="[{'headx-right':popupVisible!=false},{'headx-left':popupVisible!=true}]"></v-header>
    <mt-popup
     v-model="popupVisible"
     position="left"
     class="mtnav">
     <slot>
       <v-nav></v-nav>
    </slot>
   </mt-popup>
   <div class="login">
       <div class="label">
           <input type="text" class="txt" placeholder="请输入token" maxlength="50" v-model="token">
       </div>
       <div class="label">
           <mt-button type="primary" size="large" @click="onlog">登陆</mt-button>
       </div>
       <p>token:a7c354d4-770f-49a9-920d-66d8e0f00670</p>
   </div>
   <mt-popup
  v-model="popupVisible1"
  position="top" :modal=false class="tishi">
  <p>{{message}}</p>
</mt-popup>
</div>
</template>
<script>
import VHeader from '../components/header'
import VNav from '../components/nav'
export default {
  name: "component_name",
      components:{
    VHeader,
    VNav,
  },
  data () {
    return {
        title:'登陆',
         popupVisible:false,
         token:'',
         popupVisible1:false,
         message:''
    };
  },
  watch:{
      popupVisible1(val){
          console.log(val)
          if(val){
              setTimeout(() => {
                  this.popupVisible1=false
              }, 2000);
          }

      }
  },
  methods:{
     showclick(){
      this.popupVisible=true
    },
    onlog(){
        if(this.token===''){
            this.popupVisible1=true
            this.message='请输入正确得token'
        }else{
            console.log(this.token)
            this.$http({
                url:'https://cnodejs.org/api/v1/accesstoken',
                method:'post',
                data:(this.qs.stringify({accesstoken:this.token})).replace(/\s+/g,"")

            })
            .then((res)=>{
                // console.log({accesstoken:this.token})
                //  console.log(this.qs.stringify({accesstoken:this.token}))
                //  this.$router.push({path:'/'})
                // this.$cookiestore.setCookie('loginname',res.data.loginname,86400)
                this.$cookiestore.setCookie("loginname",res.data.loginname)
                this.$cookiestore.setCookie("avatar_url",res.data.avatar_url)
                this.$cookiestore.setCookie("token",this.token)
                this.$router.push({path:'/'})
            })
            .catch((error)=>{
                console.log(error)
            })
        }
    },
  },
}
</script>
<style scoped>
.login{
    margin-top: 40px;
    padding: 50px 15px;
}
.label{
    display: inline-block;
    width: 100%;
    margin-top: 10px;
}
.txt{
    padding: 12px 0;
    border: none;
    outline: medium;
    border-bottom:1px solid #26a2ff;
    width: 100%;
    font-size: 14px;
    color: #333;
}
.tishi{
    width: 100%;
    background: #000;
    opacity: 0.5;
    color: #fff;
    text-align: center;
    font-weight: bold;   
}
.tishi p{
    padding: 5px 0;
}
</style>


