<template>
<div>
   <v-header @showclick="showclick" :title="title" :class="[{'headx-right':popupVisible!=false},{'headx-left':popupVisible!=true}]"></v-header>
    <mt-popup
     v-model="popupVisible"
     position="left"
     class="mtnav">
     <slot>
       <v-nav></v-nav>
    </slot>
   </mt-popup>
   <div class="about">
     <ul>
       <li>
         <span>作者</span>
         <p>秦佳楠</p>
       </li>
       <li>
         <span>所以技术</span>
         <p>html,css,js,vue,axios,vue-router,mintUI</p>
       </li>
       <li>
         <span>描述</span>
         <p>这是一个重构cnode得项目</p>
       </li>
     </ul>
   </div>
</div>
</template>
<script>
import VHeader from '../components/header'
import VNav from '../components/nav'
export default {
  name: "component_name",
  data () {
    return {
        popupVisible:false,
        title:'关于',
    };
  },
  components:{
     VHeader,
     VNav,
  },
  methods:{
     showclick(){
      this.popupVisible=true
    },
  },
}
</script>
<style scoped>
.about{
  margin-top: 40px;
  padding: 0 15px;
}
.about li{
  display: block;
  padding: 10px 0;
  border-bottom: 1px solid #e1e1e1;
}
.about li span{
  display: block;
  color: #2c3e50;
  font-weight: 700;
  font-size: 16px;
  padding: 10px 0;
}
</style>


